import Vue from 'vue'
import style from './style.css'
import VueResource from 'vue-resource'

Vue.use(VueResource)

export default Vue.extend({
  data() {
    return {
      style,
    }
  },
  template: `<div>
    </div>`,

  route: {
    activate: function(transition){

      Vue.http.jsonp("https://api.douban.com/v2/book/user/wber/collections", {
        params: {
          status: 'reading'
        }
      }).then((d) => {
        console.log(d);
      })

      transition.next()
    }
  }
})




